.center {
    text-align: center;
  }
  
  .pagination {
    display: inline-block;
  }
  
  .pagination a {
    color: black;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
    transition: background-color .3s;
    border: 1px solid #ddd;
    border-radius: 100%;
    margin: 0 4px;
  }
  
  .pagination a.active {
    background-color: #696b6d;
    color: white;
    border: 1px solid #767779;
  }
  
  .pagination a:hover:not(.active) {
      background-color: #ddd;
    }